
.header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    padding:0 20px;
    
}
.first-part{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.toggle-menu{
    width:20px;
    margin:0 5px;
    display:initial;
    font-size: 10px;
    cursor: pointer;
}
.logo{
    margin:0 10px;
}
.menu{
    margin:0 10px;
    display: none;
}
.menu li{
   margin:0 10px;
   display: inline-block;
   font-weight:500;
   cursor: pointer;
   color:gray;
   padding:22px 0;
   border-bottom:3px solid transparent;
}
.menu li:hover{
    border-bottom:3px solid  hsl(26, 100%, 55%);
}
.second-part{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.info-header{
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.cart-container{
    position: relative;
}
.cart{
    width:20px;
    margin:0 10px;
    cursor: pointer;
}
.cart-div-android{
display: none;
position: absolute;
top:65px;
left:5vw;
right:5vw;
width: 90vw;
height: 200px;
border-radius: 0.2rem;
background-color: white;
border:1px solid black;
z-index: 2;
   
}
.cart-div-active{
    display: initial;
}

.cart-info{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    height:77%;
}
.cart-info p{
    color:gray;
    font-weight: bolder;
    letter-spacing:1px;
}
.cart-logo{
    border-bottom: 1px solid rgb(175, 174, 174);
    padding:5px 0;
}
.cart-logo p{
    margin:0 10px;
    font-weight:bold;
    letter-spacing:1px;
}
.avatar{
    width:25px;
}
.android-menu-container{
    position: absolute;
    top:0;
    left:-100%;
    background-color: white;
    height: 100vh;
    width: 70%;
    transition: all 0.5s;
}
.android-menu-container-active{
    left: 0;
}
.android-close{
    padding:20px;
    width:55px;
    cursor: pointer;
}
.android-menu{
    display: flex;
    flex-direction: column;
    margin-top:10px;
}
.android-menu li{
    padding:10px 0;
    color:black;
    font-weight:bolder;
    font-size:17px;
    margin-left:20px;
    letter-spacing: 1px;
}
@media screen  and (min-width:800px){
.header{
    border-bottom:1px solid rgb(159, 158, 158);
    height: 70px;
    justify-content: space-around;
}
    .toggle-menu{
    display: none;
}
.menu{
    display: initial;
}
}
@media screen and (min-width:900px){
.header{
    justify-content: space-around;
}
.menu li{
    margin:0 20px;
}
.cart{
    margin: 0 20px;
    width:20px;
}
.avatar{
    width: 40px;
}
.cart-div-android{
    width: 30vw;
    left:60vw;
    right:40vw;

}
}
.cart-div-details{
    width: 100%;
    height: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 20px;
    margin-top:20px;
}
.cart-div-img{
    width:70px;
}
.cart-div-name{
    font-weight:lighter;
    font-size:12px;
}
.cart-div-price{
    display: flex;
    column-gap: 5px;
    font-size:12px;
    align-items: center;
}
.cart-div-final-price{
   font-size: 17px;
   text-decoration: underline;
}
.cart-div-trash{
    cursor: pointer;
}
.cart-checkitout-div{
    width: 100%;
    margin-bottom: 20px;
    padding:20px;
    border-radius: 0.3rem;
    background-color: hsl(26, 100%, 55%);
    color:white;
    font-weight: bold;
    border:none;
    outline: none;
    cursor: pointer;
}
.cart-div-details-container{
    display: flex;
    flex-direction: column;
    row-gap: 20px;
   
}