.firstDiv{
    display: flex;
    flex-direction: column;
}
.main-photo{
    position: relative;
}
.main-photo .main-photo-img{
    width: 100%;
   max-width:400px;
    height: 400px;
    border-radius:1rem;
}

.spare-photos{
    margin-top: 3rem;
    display: none;
    flex-direction: row;
    justify-content:space-around;
    align-items: center;
}
.spare-photos img{
    width: 100%;
    max-width: 80px;
    height: 80px;
    border-radius: 0.3rem;
    cursor: pointer;
    margin:0 10px;
}
.spare-photos img:hover{
    opacity: 0.5;
}
.main-next, .main-previous{
    width:20px;
    position: absolute;
    cursor: pointer;
}
.main-next{
    right:0;
    top:50%;
    bottom:0;
}
.main-previous{
    left:0;
    top:50%;
    bottom:0;
}
@media screen  and (min-width:800px){
    .main-next, .main-previous{
      display: none;
    }
    .spare-photos{
        display: initial;
    }
    }