.seconddiv{
    width: 100%;
    max-width: 400px;
}
.sneaker-header{
    text-transform: capitalize;
    color: hsl(26, 100%, 55%);
    font-weight:bolder;
    font-size: 12px;
    letter-spacing: 2px;
    margin:12px 0;
}
.sneaker-info-header{
    font-size:2rem;
    margin-bottom: 30px;
}
.sneaker-para{
    color:rgb(154, 153, 153);
    letter-spacing: 1px;
    line-height:25px;
}
.sneaker-price-discount{
    display: flex;
    column-gap: 10px;
    margin:20px 0;
    align-items: center;
}
.sneaker-price-discount p{
    font-size: 25px;
    font-weight: bolder;
}
.sneaker-price-discount span{
    color:hsl(26, 100%, 55%);
    font-weight: bolder;
}
.sneaker-real-price{
    text-decoration: line-through;
    color:rgb(154, 153, 153);
}
.sneaker-order-info{
    margin-top:30px;
    margin-bottom: 50px;
    align-items: center;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}
.sneaker-count{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-around;
}
.minute{
  margin-right: 25px;
}
.plus{
    margin-left: 25px;
}
.number{
    font-weight: bolder;
}
.add-to-chart{
    width: 100%;
    display: flex;
    align-items: center;
    margin:0 20px;
    padding:10px 20px;
    border-radius:0.5rem;
    background-color:hsl(26, 100%, 55%) ;
    color:white;
    cursor: pointer;
    justify-content: center;
}
.sneaker-cart{
    color:white;
    width: 20px;
    margin:0 10px;
   
}